import React from 'react';
import './App.css';
import { marked } from 'marked';

marked.setOptions({
    breaks: true,
    gfm: true
});

const markRender = new marked.Renderer();
markRender.link = function (href, title, text) {
    return `<a target="_blank" href="${href}">${text}</a>`;
};

const placeholder = `# This is an H1 element
## This is an H2 element
Here is a \`<div></div>\` and
\`\`\`
function() {
    return 'Hello World';
}
\`\`\`
some multi-line code.
Here is a [link](https://developer.mozilla.org/en-US/docs/Web/HTML/Element/blockquote) about making
> Block Quotes

![dog](https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRZExHXJ_X10R3xGjuby9o0_ShywAC18XtKyQ&usqp=CAU)
- **NFT's are a Ponzi Scheme**
  - Please don't purchase NFT's

`;

class Editor extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            editorText: placeholder
        };
        this.charTyped = this.charTyped.bind(this);
    };
    charTyped(event) {
        this.setState((state) => {
            return { editorText: event.target.value };
        });
    };

    render() {
        return (
            <div>
                <div class='DisplayBoth'>
                    <div>
                        <div class='editorBanner'><span>Editor</span></div>
                        <div class='editContainer'>
                            <textarea class='editorText' id='editor' value={this.state.editorText} onChange={this.charTyped} />
                        </div>
                    </div>
                    <div class='floatRight'>
                        <div class='previewBanner'><span>Previewer</span></div>
                        <div class='previewContainer' id='preview' dangerouslySetInnerHTML={{ __html: marked(this.state.editorText, { renderer: markRender }) }}></div>
                    </div>
                </div>
            </div>
        );
    };
};
export default Editor;